import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Button, Row, Col, ListGroup, Image, Card } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Message from "../components/Message";
import CheckoutSteps from "../components/CheckoutSteps";
import { createOrder } from "../actions/orderAction";

const PlaceOrderScreen = ({ history }) => {
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const dispatch = useDispatch();

  const cart = useSelector((state) => state.cart);

  const addDecimals = (num) => {
    return (Math.round(num * 100) / 100).toFixed(2);
  };

  cart.itemsPrice = addDecimals(
    cart.favouriteItems.reduce((acc, item) => acc + item.price * item.qty, 0)
  );
  const [sp, setSp] = useState(0);
  useEffect(() => {
    // if (cart.paymentMethod === "Cash on delievery") {
    if ((cart.itemsPrice < 500) | (cart.itemsPrice === 500)) {
      setSp(50);
    } else {
      setSp(0);
    }
    // }
    // else {
    //   if (cart.itemsPrice > 500) {
    //     setSp(0);
    //   } else {
    //     setSp(50);
    //   }
    // }
  }, [cart.itemsPrice, cart.paymentMethod]);
  // if (cart.paymentMethod === "Cash on delievery") {
  //   if (cart.itemsPrice > 500) {
  //     setSp(100);
  //   }
  // } else {
  //   if (cart.itemsPrice > 500) {
  //     setSp(0);
  //   } else {
  //     setSp(50);
  //   }
  // }

  // cart.shippingPrice = addDecimals(cart.itemsPrice > 100 ? 0 : 100);
  cart.shippingPrice = sp;
  // cart.taxPrice = addDecimals(Number((0.15 * cart.itemsPrice).toFixed(2)));
  cart.taxPrice = 0;
  cart.totalPrice = (
    Number(cart.itemsPrice) +
    Number(cart.shippingPrice) +
    Number(cart.taxPrice)
  ).toFixed(2);

  const orderCreate = useSelector((state) => state.orderCreate);
  const { order, success, error } = orderCreate;

  const placeOrderHandler = () => {
    dispatch(
      createOrder({
        orderItems: cart.favouriteItems,
        shippingAddress: cart.shippingAddress,
        paymentMethod: cart.paymentMethod,
        itemsPrice: cart.itemsPrice,
        shippingPrice: cart.shippingPrice,
        taxPrice: cart.taxPrice,
        totalPrice: cart.totalPrice,
      })
    );
  };

  useEffect(() => {
    if (!userInfo) {
      history.push("/login");
    }
    if (success) {
      history.push(`/order/${order._id}`);
      // //       // dispatch({ type: USER_DETAILS_RESET })
      // //       // dispatch({ type: ORDER_CREATE_RESET })
    }
    // eslint-disable-next-line
  }, [history, success]);

  return (
    <div>
      {userInfo && (
        <div>
          <>
            <CheckoutSteps step1 step2 step3 />
            <Row>
              <Col md={8}>
                <ListGroup variant="flush">
                  <ListGroup.Item>
                    <h2>Shipping</h2>
                    <p>
                      <strong>Address:</strong>
                      {cart.shippingAddress.address},{" "}
                      {cart.shippingAddress.city} , {cart.shippingAddress.state}
                      {cart.shippingAddress.postalCode},{" "}
                      {cart.shippingAddress.country}
                    </p>
                  </ListGroup.Item>

                  <ListGroup.Item>
                    <h2>Payment Method</h2>
                    <strong>Method: </strong>
                    {cart.paymentMethod}
                  </ListGroup.Item>

                  <ListGroup.Item>
                    <h2>Order Items</h2>
                    {cart.favouriteItems.length === 0 ? (
                      <Message>Your cart is empty</Message>
                    ) : (
                      <ListGroup variant="flush">
                        {cart.favouriteItems.map((item, index) => (
                          <ListGroup.Item key={index}>
                            <Row>
                              <Col md={1}>
                                <Image
                                  src={item.images[0].url}
                                  alt={item.name}
                                  fluid
                                  rounded
                                />
                              </Col>
                              <Col>
                                <Link to={`/product/${item.product}`}>
                                  {item.name}
                                </Link>
                              </Col>
                              <Col md={4}>
                                {item.qty} x ₹{item.price} = ₹
                                {item.qty * item.price}
                              </Col>
                            </Row>
                          </ListGroup.Item>
                        ))}
                      </ListGroup>
                    )}
                  </ListGroup.Item>
                </ListGroup>
              </Col>
              <Col md={4}>
                <Card>
                  <ListGroup variant="flush">
                    <ListGroup.Item>
                      <h2>Order Summary</h2>
                    </ListGroup.Item>
                    <ListGroup.Item>
                      <Row>
                        <Col>Items</Col>
                        <Col>₹{cart.itemsPrice}</Col>
                      </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                      <Row>
                        <Col>Shipping</Col>
                        <Col>₹{cart.shippingPrice}</Col>
                      </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                      <Row>
                        <Col>Tax</Col>
                        <Col>₹{cart.taxPrice}</Col>
                      </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                      <Row>
                        <Col>Total</Col>
                        <Col>₹{cart.totalPrice}</Col>
                      </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                      {error && <Message variant="danger">{error}</Message>}
                    </ListGroup.Item>
                    <ListGroup.Item>
                      <Button
                        type="button"
                        className="btn-block"
                        disabled={cart.favouriteItems === 0}
                        onClick={placeOrderHandler}
                      >
                        Place Order
                      </Button>
                    </ListGroup.Item>
                  </ListGroup>
                </Card>
              </Col>
            </Row>
          </>
        </div>
      )}
    </div>
  );
};

export default PlaceOrderScreen;
